body.post-type-dp_intro_tours {
	.acf-table-root {
		padding: 0;
		margin: 0;
	}

	// leave default for mobiles
	@include atHoverAbleDev {
		.dpit-scrollbar {
			scrollbar-width: thin;
			scrollbar-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.7) rgba(var(--dpit-uac-menu-a-item-bg), 0.2);

			&::-webkit-scrollbar {
				height: 10px;
				width: 10px;
			}

			&::-webkit-scrollbar-track {
				border-radius: 4px;
				background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.2);
			}

			&::-webkit-scrollbar-thumb {
				border-radius: 4px;
				background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.7);
			}
		}
	}

	.acf-table-wrap {
		overflow-x: auto;
		overflow-y: hidden;
		height: 100%;
		-webkit-overflow-scrolling: touch;

		&--cs {

			.acf-table-body-cont {
				@import './../../../../includes/assets/css/editor-content-color';
				@import './../../../../includes/assets/css/editor-content';
			}
		}
	}

	/* TABLE { */

	.acf-table-table {
		display: table;
		border-collapse: collapse;
		width: 100%;
		height: 100%;
		cursor: grab;

		.acf-icon {
			position: relative;
			display: flex;
			align-content: center;
			align-items: center;
			justify-content: center;
			transform-origin: center;
			@include transitionMultiFasterX(background-color);
			border: none !important;


			&:hover {
				border: none !important;
			}

			&--s {
				width: 27px;
				height: 27px;
				font-size: 17px;

				&.acf-icon-minus {
					width: 26px;
					height: 26px;
				}
			}

			&--xs {
				width: 23px;
				height: 23px;
				font-size: 13px;

				&.acf-icon-minus {
					width: 22px;
					height: 22px;
				}
			}

			&--xxs {
				width: 21px;
				height: 21px;
				font-size: 12px;

				&.acf-icon-minus {
					width: 20px;
					height: 20px;
				}
			}

			&.acf-icon-plus {
				background-color: rgb(var(--dpit-uac-prim-bg));
				color: rgb(var(--dpit-uac-prim));

				&:hover {
					background-color: rgba(var(--dpit-uac-prim-bg), 0.9);
				}

			}

			&.acf-icon-minus {
				background-color: rgb(var(--dpu-color-r));
				color: rgb(var(--dpu-color-r-text));

				&:hover {
					background-color: rgba(var(--dpu-color-r), 0.8);
				}

			}
		}
	}



	.acf-table-body-row {
		width: 100%;
	}


	/* TOP { */

	.acf-table-top-row {
		display: none;
	}


	.acf-table-top-left {
		position: relative;
		display: table-cell;
		width: 1px;
		padding: 4px 8px 4px 16px;
		min-height: 27px;
	}

	.acf-table-top-cell {
		position: relative;
		display: table-cell;
		padding: 4px 8px;
		border: 1px solid #e1e1e1;
		background-color: #eee;
		cursor: move;
		min-width: 68px;
	}

	.acf-table-top-right {
		position: relative;
		display: table-cell;
		width: 26px;
	}

	.acf-table-top-cont {
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		text-align: center;
		color: #aaa;
	}

	/* TOP } */

	/* HEADER { */

	.acf-table-header-row {
		position: relative;
		display: table-row;
	}

	.acf-table-header-left {
		position: relative;
		display: table-cell;
	}

	.acf-table-header-cont {
		user-select: none;
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		font-weight: bold;
	}

	.acf-table-header-cell {
		position: relative;
		display: table-cell;
		text-align: center;
		padding: 4px 8px;
		border: 1px solid #e1e1e1;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 0.8);
		color: rgb(var(--dpit-uac-main-menu));
		//height: 28px;

		&--with-info-icon {

			padding-right: 20px;

			.acf-table-header-cont {
				margin-top: 15px;
				margin-bottom: 10px;
			}
		}
	}

	.acf-table-header-right {
		position: relative;
		display: table-cell;
		width: 26px;
	}


	/* HEADER } */

	/* BODY { */

	.acf-table-body-row {
		position: relative;
		display: table-row;
	}

	.acf-table-body-left {
		position: relative;
		display: table-cell;
		border: 1px solid #e1e1e1;
		padding: 4px 8px 4px 8px;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 0.8);
		color: rgb(var(--dpit-uac-main-menu));

		min-height: 27px;
		min-width: 40px;
		cursor: ns-resize;
		text-align: right;
		vertical-align: middle;


		.acf-table-body-left-wrap {
			display: flex;
			justify-content: left;
			align-items: center;
			@include transitionFaster(transform);

			.acf-table-body-cont {
				padding-left: 5px;
				padding-right: 3px;
				padding-bottom: 5px;
				font-weight: 700;
				font-size: 18px;
				color: rgb(var(--dpit-uac-main-menu));
				line-height: 1.6;
			}

			.acf-table-sort-row-icon {
				width: 10px;
				height: auto;
			}

			&:hover {
				transform: scale(1.1);

				.acf-table-sort-row-icon {


					svg {
						path {

							fill: rgb(var(--dpit-uac-main-menu));
						}
					}

				}

			}
		}



	}

	.acf-table-body-cell {
		position: relative;
		display: table-cell;
		border: 1px solid #e1e1e1;
		padding: 4px 8px;
		background-color: white;


		@include atXXS {
			max-width: calc(90vw - 30px);
		}

		&:not(.acf-table-body-cell--text_wp_edit):not(.acf-table-body-cell--text) {
			text-align: center;
		}

		&__title {
			text-transform: uppercase;
			cursor: grab;
			letter-spacing: 0.3px;
			font-size: 1em;
			font-weight: 600;
		}

		.acf-table-body-cont {
			cursor: pointer;
			min-width: 100px;

			@include atTouchDev {
				min-height: 35px !important;
			}
		}

		&--text {
			cursor: pointer;
			min-width: 150px;
			text-align: left !important;
			position: relative;

			.acf-table-body-cont {
				padding: 5px;
				min-height: 50px;
			}
		}

		&--checkbox {
			cursor: grab;

			.acf-table-body-cont {
				cursor: grab;
			}
		}

		&--text_wp_edit {
			cursor: pointer;

			.acf-table-body-cont {
				min-width: 350px;

				@include atXXS {
					min-width: 300px;
				}

				pre {
					white-space: pre-wrap;
				}

			}
		}

		&--link {
			&:not(.acf-table-body-cell--link-multi) {
				cursor: pointer;
			}


			.acf-table-body-cont {
				min-width: 100px;

				@include atXXS {
					min-width: 100px;
				}


			}
		}

	}

	.acf-table-body-right {
		position: relative;
		display: table-cell;
		border-bottom: 1px solid transparent;
		min-width: 30px;
		vertical-align: middle;
	}

	.acf-table-body-cont {
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		white-space: pre-wrap;
		user-select: none;

		&--wsn {
			white-space: normal;
		}
	}

	.acf-table-body-cont-li {
		display: flex;
		align-items: center;
		font-weight: 600;
		justify-content: flex-end;
		min-width: 100%;

		padding: 0 5px;
		gap: 5px;

		.acf-icon {
			flex-basis: 21px;
			margin-left: 5px;
			margin-right: 8px;
			flex-shrink: 0;
		}

		span {
			line-height: 1.6;
			color: $text-color-dark;
			text-decoration: underline;
		}

		margin-bottom: 10px;
	}



	.acf-icon.acf-table-add-link {
		margin: 0 auto;

		&:not(:hover) {
			border-color: transparent;
		}

	}

	.acf-table-cell-ec {
		margin: 10px -4px 0;
		padding: 5px 0 3px;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 0.8);
		color: rgb(var(--dpit-uac-main-menu));
		border-radius: 2px;
		text-align: left;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		flex-wrap: wrap;
		min-width: 180px;
		cursor: drag;


		.acf-cell-eci-wrap {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			padding: 8px 8px;
			width: 100%;

			input {
				&[type="number"] {
					cursor: pointer;
					order: 3;
					flex-direction: column;
					margin-top: 5px;
					max-width: 75%;
				}

				&[type="checkbox"] {
					cursor: pointer;
					order: 0;
				}

			}

			span {
				order: 1;
				font-size: 11px;
				text-transform: uppercase;
				margin-bottom: 0;
				white-space: nowrap;
			}

			.dpit-info-icon {
				order: 2;
				margin: 0 15px 0 auto;
				//justify-self: flex-end;
			}



		}

		.acf-table-body-cont-sub {
			margin-top: 0;
		}

		&--hidden {
			opacity: 0;
			position: absolute;
			pointer-events: none;
		}
	}

	/* BODY } */

	/* BOTTOM { */

	.acf-table-bottom-row {
		position: relative;
		display: table-row;

	}

	.acf-table-bottom-left {
		position: relative;
		display: table-cell;
		height: 24px;
	}

	.acf-table-bottom-cell {
		position: relative;
		display: table-cell;
	}

	.acf-table-bottom-right {
		position: relative;
		display: table-cell;
	}

	/* BOTTOM } */


	/* TABLE } */

	/* BUTTONS { */

	.acf-table-add-col {
		box-sizing: content-box;
		position: absolute !important;
		top: -9px;
		right: -10px;
		z-index: 1;
	}

	.acf-table-add-row {
		box-sizing: content-box;
		position: absolute !important;
		bottom: -9px;
		left: -10px;
		z-index: 1;
	}

	.acf-table-remove-col {
		box-sizing: content-box;
		position: absolute !important;
		top: 4px;
		left: 50%;
		margin-left: -10px;
	}

	.acf-table-remove-row {
		box-sizing: content-box;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-left: 5px;
	}



	/* BUTTONS } */

	/* CELL EDITOR { */

	.acf-table-cell-editor {
		min-width: 300px;
		display: none;
		padding: 5px;
	}

	.acf-table-cell-editor-textarea {
		border-radius: 2px;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 0.2);
		color: rgba($text-color-dark, 1);
	}


	/* CELL EDITOR } */

	/* OPTIONBOX { */

	.acf-table-optionwrap {
		display: none;
	}

	.acf-table-optionbox {
		margin-top: 16px;
		margin-bottom: 16px;
	}

	.acf-table-optionbox+.acf-table-optionbox {
		margin-top: -20px;
	}

	/* FIELDS { */

	.acf-table-optionbox label {
		display: inline-block;
		font-weight: bold;
		padding-right: 6px;
		padding-bottom: 10px;
		padding-top: 10px;
	}

	.acf-table-fc-opt-use-header {
		width: auto !important;
		padding: 3px 24px 3px 8px !important;
		/* fixes ACF padding 2px for select fields */
	}

	.acf-table-fc-opt-caption {
		width: 100% !important;
	}

	/* FIELDS } */

	/* OPTIONBOX } */

	/*{ ADJUSMENTS*/



	.acf-table-wrap {
		padding-left: 10px;
		padding-right: 10px;
	}





	.dp-hidden {
		display: none !important;
	}

	.acf-table-remove-col,
	.acf-table-add-col {
		display: none !important;
	}


	.acf-table-body-cont--select>.acf-table-body-cont-sub {
		min-width: 80px;
	}


	#dpit-admin-first-step-link-cell {
		transition: background-color $transition-time-med ease;
		background-color: rgba(var(--dpit-uac-main-menu-bg), 0.3);
		color: $text-color-dark;
		padding-bottom: 12px;

		&.dpit-admin-first-step-empty {
			background-color: rgba(var(--dpu-color-r), 0.351);
			color: $text-color-dark;
		}

		#dpit-admin-step-link-cell-everywhere {
			display: none;
		}


		&.dpit-admin-first-step-everywhere {
			cursor: pointer;
			user-select: none;
			background-color: rgba(var(--dpit-uac-badge-bg), 0.4);
			//min-height: 200px !important;

			.acf-table-body-cont .acf-table-add-link,
			.acf-table-body-cont ul {
				display: none;
			}

			#dpit-admin-step-link-cell-everywhere {
				opacity: 1;
				display: flex;

				justify-content: center;
				align-items: center;
				color: $text-color-dark;
				font-weight: 600;

				//font-size: 1.1em;
				padding: 10px 5px 20px;
			}
		}

	}


	.dpit-table-pro-link {
		opacity: 0.85;
		color: rgb(var(--dpit-uac-badge-bg));
		text-decoration: none;
		@include transitionFaster(opacity);

		&:hover {
			opacity: 1;
		}
	}

	/*.acf-table-body-cell--link-all-pages {
		pointer-events: none;
		position: relative;

		.acf-table-body-cont {
			display: none;
		}

		&::after {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			content: '$EVERYWHERE';
			background-color: rgba(var(--dpu-color-r), 0.351);
			color: rgba(var(--dpu-color-1), 1);
			font-weight: 700;
		}
	}*/


	// touch devices
	@media (hover: none) {


		.acf-table-body-left {
			min-width: 50px;
		}

		.acf-table-body-cont--select>.acf-table-body-cont-sub {
			height: 40px;
		}

		.acf-table-wrap {
			padding: 11px 36px 11px 20px;

			.acf-table-add-row {
				left: -19px;
				bottom: -19px;
			}
		}

	}

}


/*ADJUSMENTS }*/