.post-type-dp_intro_tours {

	@import './dpit-acf-normalize';

	.acf-label--with-info-icon {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 5px;
	}


	.acf-label {
		.description {
			div {
				height: 5px;
			}
		}
	}

	#titlewrap {
		padding-top: 25px !important;

		#title-prompt-text {
			@include atXXS {
				font-size: 14px;
				white-space: nowrap;
			}
		}
	}

	.select2-results {
		&__options {
			@include atHoverAbleDev {
				scrollbar-width: thin;
				scrollbar-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.6) rgba(var(--dpit-uac-menu-a-item-bg), 0.2);

				&::-webkit-scrollbar {
					height: 8px;
					width: 8px;
				}

				&::-webkit-scrollbar-track {
					border-radius: 4px;
					background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.2);
				}

				&::-webkit-scrollbar-thumb {
					border-radius: 4px;
					background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.6);
				}
			}
		}

		&__option {
			&--highlighted {
				color: rgb(var(--dpit-uac-menu-a-item)) !important;
				background-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
			}
		}
	}


	#post-body {

		.acf-postbox {

			&.dpit-acf-tippy {

				&>.acf-fields {
					padding: 10px 20px !important;

					.acf-field {


						//grid-column: 1 / span 6; // default
						&.acf-field-select {
							.select2-container {
								.select2-selection {
									&:focus {
										outline: none !important;
										border: rgb(var(--dpit-uac-sec-border)) solid 1px !important;
									}

									&__rendered {
										color: $text-color-dark !important;
										font-weight: 500 !important;
									}
								}
							}
						}

						&.acf-field-relationship {
							.acf-relationship {
								.list {
									.acf-rel-item:hover {
										background: rgba(var(--dpu-color-4), 0.89) !important;
										color: rgba(var(--dpu-color-4-text), 1) !important;
									}
								}

								.values .acf-rel-item .acf-icon[data-name="remove_item"] {
									top: 3px;
								}

								.selection .values .acf-rel-item {
									cursor: default !important;
								}
							}
						}

						&.acf-field-number,
						&.acf-field-text {
							.acf-input {
								input {

									color: $text-color-dark !important;
									font-weight: 500 !important;
									box-shadow: none !important;

									&::placeholder {
										font-weight: 400 !important;
										color: $text-color-placeholder;
									}
								}
							}
						}

						&.acf-field-color-picker {
							.wp-picker-container {
								.wp-color-result {

									&:focus {
										outline: 2px solid rgb(var(--dpit-uac-sec-border)) !important;
										border: unset !important;
										box-shadow: unset !important;
									}
								}
							}
						}

						&.acf-field-true-false {
							&:not(.acf-field--main) {
								display: flex;
								flex-direction: row-reverse;
								align-items: center;
								justify-content: flex-end;


								.acf-label {
									flex-basis: 100%;
									margin: 0 0 0 10px;
									margin-bottom: 0;

									label {
										margin-bottom: 0;
									}
								}
							}

							.acf-input {
								flex-shrink: 0;

								.acf-true-false {

									.acf-switch {
										@include transitionMultiFast(border, background-color);
										border: rgba(var(--dpit-uac-menu-a-item-bg), 0.95) solid 1px !important;
										background: none !important;
										background-color: rgba(var(--dpit-uac-main-menu-bg), 0.05) !important;

										&.-on {

											background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.95) !important;

											&:hover {
												background-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.75) !important;
											}

											.acf-switch-slider {
												border-color: rgb(var(--dpit-uac-menu-a-item-bg)) !important;
											}

										}

										.acf-switch-on {
											color: rgba(var(--dpit-uac-menu-a-item), 1) !important;

										}

										.acf-switch-off {
											color: $text-color-dark !important;
										}

										.acf-switch-slider {
											border-color: rgba(var(--dpit-uac-menu-a-item-bg), 0.7) !important;

											&:hover {
												border-color: rgba(var(--dpit-uac-menu-a-item-bg), 1) !important;
											}
										}
									}
								}
							}

						}

						&.acf-field-group {
							display: flex;
							flex-direction: column;
							border: none !important;

							>.acf-label {
								>label {
									font-size: 18px;
									text-transform: uppercase;
								}
							}
						}

						&--main {
							>.acf-label {
								>label {
									font-size: 18px;
									text-transform: uppercase;
								}
							}
						}


						&:not(.acf-field--no-info-icon):not(.acf-field-group) {
							.acf-label {
								.description {
									visibility: hidden;
								}
							}

							span.message {
								visibility: hidden;
							}
						}

						// touch devices
						@media (hover: none) {

							.acf-rel-item {
								min-height: 25px !important;
								font-size: 15px;
								padding-top: 10px !important;
								padding-bottom: 6px !important;
							}

							.acf-icon {
								&--s {
									width: 36px;
									height: 36px;
									font-size: 26px;
								}
							}

							.acf-relationship .values .acf-rel-item .acf-icon[data-name="remove_item"] {
								display: inline-block;
								background: rgba(150, 101, 45, 0.841);
								width: 30px;
								height: 30px;
								line-height: 30px;
								font-size: 20px;
								top: 4px;
							}


							.select2-container {
								font-size: 18px;

								.select2-selection:not(.select2-selection--multiple) {

									min-height: 40px;

									.select2-selection__rendered {
										height: 40px;
										line-height: 40px !important;

									}

									.select2-selection__arrow {
										height: 40px !important;

										b {
											border-width: 7px 5px 0 5px !important;
											left: 30% !important;
											border-color: rgb(var(--dpit-uac-menu-a-item-bg)) transparent transparent transparent !important;
										}
									}
								}

								.select2-selection.select2-selection--multiple {
									.select2-selection__rendered {
										display: flex;
										padding: 5px 7px 8px 10px;
										flex-direction: column;
										gap: 5px;

										@include atXXS {}

										.select2-selection__choice {
											padding: 5px 15px 5px 10px;
											box-sizing: content-box;
											//display: flex;
											//align-items: center;
											//gap: 5px;
											float: unset;
											position: relative;

											.select2-selection__choice__remove {
												position: absolute;
												right: 5px;
												bottom: 0;
												font-size: 25px;
												//max-height: 20px;
												display: inline-block;
												text-transform: uppercase;
												line-height: 1;

											}

											.acf-selection {
												font-size: 15px;
												line-height: 1;
											}

											.select2-results__option {
												padding: 12px;
											}
										}
									}



								}
							}


						}
					}
				}


				// responsive
				&>.acf-fields {
					display: grid;
					grid-template-columns: 16.666% 16.666% 16.666% 16.666% 16.666% 16.666%;
					grid-template-rows: auto auto auto auto auto auto;
					row-gap: 10px;

					@include atL {
						display: block;
					}

					@include atS {
						padding: 8px 5px !important;
					}


					@include atXXS {
						padding: 5px 0px !important;
					}

					.acf-field {

						&.acf-field-table {
							@include atXXS {
								padding: 15px 0;
							}
						}

						&.acf-field-radio {
							@include atS {
								.acf-radio-list {
									li {
										display: block;
										float: none;
										padding-bottom: 10px;
									}
								}
							}
						}

						&.acf-field--main {
							grid-column: 1 / span 6;
							border: none !important;

							.acf-input {
								.acf-switch span {
									font-size: 15px;
								}
							}

							&[data-name="intro_enabled"] {
								grid-column: 1 / span 2;
								min-width: 200px;
							}

							&[data-name="add_additional_trigger"] {
								grid-column: 4 / span 3;
								grid-row: 3 / span 1;
								max-height: 150px;
							}
						}



						&[data-name="intro_steps"] {
							grid-column: 1 / span 6;

							.acf-label {
								margin-bottom: 0;
							}

							@include atXXS {
								padding-left: 16px;
								padding-right: 16px;
							}
						}

						&[data-name="intro_related_posts_multipage"] {
							grid-column: 1 / span 6;
						}

						&[data-name="plugin_version"] {
							grid-column: 1 / span 2;
						}

						&.acf-field-group {
							&[data-name="intro_design"] {
								grid-column: 1 / span 2;
							}

							&[data-name="dp_tour_behaviour"] {
								grid-column: 3 / span 4;

								.acf-fields {
									display: grid;
									grid-template-columns: 50% 50%;

									@include atL {
										display: block;
									}
								}

							}

							&[data-name="intro_labeling"] {
								grid-column: 1 / span 3;
							}

							&[data-name="intro_url_variables"] {
								grid-column: 4 / span 3;

								.acf-field[data-name="url_vars_examples"] textarea {
									height: 105px;
									cursor: pointer;

									&.url-vars-disabled {
										pointer-events: none;
										opacity: 0.6;
									}
								}
							}

							&[data-name="intro_trigger"] {
								grid-column: 1 / span 3;
								grid-row: 3 / span 1;
							}

							&[data-name="intro_trigger_2"] {
								top: 100px;
								padding-bottom: 100px;
								grid-column: 4 / span 3;
								grid-row: 3 / span 1;

								@include atL {
									top: 0;
									padding-bottom: 15px;
								}
							}
						}
					}
				}

				&.columns-1 {
					.acf-postbox>.acf-fields {
						@include atS {
							display: block;
						}


						.acf-field {
							&.acf-field-group {
								@include atS {
									&[data-name="intro_trigger_2"] {
										top: 0;
										padding-bottom: 0;
									}

									&[data-name="dp_tour_behaviour"] {
										.acf-fields {
											display: block;
										}
									}
								}
							}
						}
					}
				}

			}
		}

	}

}