#post-body-content {

	.dpit-admin-btn {

		// in admin edit when first step url is not valid
		&--undef:not(.dpit-admin-btn--anim) {
			background-color: rgba(var(--dpu-color-r), 0.3);
			outline: solid 1px rgba(var(--dpu-color-r), 0.8);

			.dpit-admin-btn__content {
				font-weight: 400;
				display: block;
			}

			&:hover {
				background-color: rgba(var(--dpu-color-r), 0.25) !important;
				outline: solid 1px rgb(var(--dpu-color-r)) !important;
			}
		}

		&__content {

			&--undef-start,
			&--undef-vars {
				text-transform: none;
				font-size: 1em;
				color: rgba($text-color-dark, 1);
			}
		}
	}


	.dpit-admin-pulse-anim {
		border-collapse: unset;
		animation: dpit-admin-pulse 1s !important;
	}

	.dpit-admin-err-highlight {
		background-color: rgba(var(--dpu-color-r), 0.3) !important;
	}
}

#wpbody .notice.updated,
#wp-admin-bar-view,
#wp-admin-bar-preview,
#post-preview {
	display: none !important;
}



@keyframes dpit-admin-pulse {
	0% {
		transform: scale(0.98);
		box-shadow: 0 0 0 0 rgba(var(--dpu-color-3), 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(var(--dpu-color-3), 0);
	}

	100% {
		transform: scale(0.98);
		box-shadow: 0 0 0 0 rgba(var(--dpu-color-3), 0);
	}
}